import React, {
  FormEvent,
  startTransition,
  useActionState,
  useState,
} from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import dynamic from "next/dynamic";
import { Button } from "../ui/button";
import "react-quill-new/dist/quill.snow.css";
import { createArticle } from "@/actions/create-article";
import { Article } from "@prisma/client";
import Image from "next/image";

const ReactQuill = dynamic(() => import("react-quill-new"), { ssr: false });

type EditArticleProps = {
  article: Article;
};

const EditArticlePage: React.FC<EditArticleProps> = ({ article }) => {
  const [content, setContent] = useState("");
  const [formState, action, isPending] = useActionState(createArticle, {
    errors: {},
  });

  //to send the written content data
  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const formData = new FormData(e.currentTarget);
    formData.append("content", content);
    startTransition(() => {
      action(formData);
    });
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <Card>
        <CardHeader>
          <CardTitle> Create New Article </CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="space-y-2">
              <Input
                type="text"
                name="title"
                defaultValue={article.title}
                placeholder="Enter an article title"
              />
              {formState.errors.title && (
                <span className="text-red-600 text-sm">
                  {" "}
                  {formState.errors.title}{" "}
                </span>
              )}
            </div>
            <div className="space-y-2">
              <Label> Category </Label>
              <select
                className="flex h-10 w-full rounded-md"
                name="category"
                defaultValue={article.category}
                id="category"
              >
                <option value="others"> Select Category </option>
                <option value="technology"> Technology </option>
                <option value="programming"> Programming </option>
                <option value="web-development"> Web Development </option>
              </select>
              {formState.errors.category && (
                <span className="text-red-600 text-sm">
                  {" "}
                  {formState.errors.category}{" "}
                </span>
              )}
            </div>
            <div className="space-y-2">
              <Label htmlFor="ftdImg"> Featured Image </Label>
              <Input type="file" id="ftd-img" name="ftdImg" accept="image/*" />
              <Image src={article.featuredImage} alt="ftd-img" className="w-48 h-32 object-cover rounded-md"/>
            </div>
            <div className="space-y-2">
              <Label> Content </Label>
              <ReactQuill theme="snow" value={content} onChange={setContent} />
              {formState.errors.content && (
                <span className="text-red-600 text-sm">
                  {" "}
                  {formState.errors.content[0]}{" "}
                </span>
              )}
            </div>
            <div className="flex justify-end gap-4">
              <Button variant={"outline"}> Cancel </Button>
              <Button type="submit" disabled={isPending}>
                {isPending ? "Loading..." : "Publish Article"}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};

export default EditArticlePage;
